@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600);
*{box-sizing: border-box;}

html, body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
	font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


ul, li{list-style: none;margin: 0;padding: 0;}

p, h1, h2, h3 ,h4 ,h5, h6{margin: 0;padding: 0;}

a{text-decoration: none;color: #000;color: initial;}
a:hover{color: #00AB44;}

.container{width: 100%;max-width: 1400px;padding-left: 15px;padding-right: 15px;padding-top: 50px;}
.HeaderWrap ul{display: flex;justify-content: space-between;}
.HeaderWrap{padding: 15px;}
.active{font-weight: 700;}
.active a{color: #00AB44;}
.onlyMob{display: none;}


@media only screen and (max-width:576px){
    .container{padding-top: 100px;}
    .HeaderWrap{
        transition: all .3s;
        right: 100%;
        position: fixed;
        background: #fff;
        width: 100%;
        height: 100vh;}
    .HeaderWrap.headerOpen {right: 0;}
    .HeaderWrap.headerOpen li{line-height: 3;}
        .HeaderWrap ul{display: none;}
    .HeaderWrap.headerOpen ul{display: flex;flex-direction: column;}
    .onlyMob{display: block;}
    #menu-toggle{    display: flex;
        align-items: center;
        flex-direction: column;
        position: fixed;
        top: 30px;
        right: 30px;}
    #menu-toggle .bar{
        width: 40px;
        height: 2px;
        background-color: #62656a;
        margin: 4px 0;
        transition: 0.4s;
        border-radius: 2px;
        z-index: 2;
    }
    #menu-toggle.open .bar3 {
        display: none;
    }
    #menu-toggle.open .bar2 {-webkit-transform: rotate(45deg) translate(-3px, 1px);transform: rotate(45deg) translate(-3px, 1px);}
    #menu-toggle.open .bar1 {-webkit-transform: rotate(-45deg) translate(-7px, 3px);transform: rotate(-45deg) translate(-7px, 3px);}
}
.aboutSection{padding-left: 15px;margin-top: 50px;}
.aboutSection li{margin: 10px 0;list-style-type: disc;}
.NameWrap h3{margin: 0 0 10px;color: #00AB44;}
.NameWrap a{font-size: 14px;}
.aboutSection h2{text-transform: uppercase;color: #00AB44;}
.clientWrap{display: flex;flex-wrap: wrap;}
.clientWrap p{margin-bottom: 30px;flex-basis: calc(25% - 20px);max-width: calc(25% - 20px);margin-left: 10px;margin-right: 10px;font-weight: 500;}

@media only screen and (max-width:576px){
    .clientWrap p{flex-basis: calc(100% - 20px);max-width: calc(100% - 20px);margin-left: 0;}
}
.ExperienceWrap{display: flex;flex-direction: column;}
.companyDetails{margin-bottom: 30px;}
.companyName{display: flex;align-items: center;}
.companyDetails a{margin: 0 0 10px;display: block;}
.ExperienceWrap h2{margin-bottom: 50px;color: #00AB44;}

@media only screen and (max-width:576px){
    .companyName{
        flex-direction: column;
        align-items: flex-start;
    }
    .companyName h3:first-child{font-size: 24px;}
    .companyName h3{font-size: 16px;}
    .companyName p{margin-left: -4px;}
}
.OthersWrap h2 {
    margin-bottom: 10px;
    text-transform: uppercase;
    color: #00ab44;
}
.OthersWrap .certificationsWrap {
    margin-top: 50px;
    margin-bottom: 50px;
}
.OthersWrap .coCurrWrap ul {
    padding-left: 15px;
}
.OthersWrap .coCurrWrap li {
    list-style-type: disc;
    margin-bottom: 5px;
}

.projectSubWrap{display: flex;justify-content: space-between;flex-wrap: wrap;}
.prjctWrap{margin-bottom: 40px;flex-basis: calc(33.33% - 20px);max-width: calc(33.33% - 20px);margin-left: 10px;margin-right: 10px;}
.projectSubWrap a{margin: 10px 0 0;display: block;font-weight: 500;}
.ProjectsWrap h2{margin-bottom: 50px;text-transform: uppercase;color: #00AB44;}
.prjctWrap h3{color: #00AB44;}
.prjctWrap h3 span{color: #000;font-size: 16px;}



@media only screen and (max-width:576px){
    .prjctWrap{flex-basis: calc(100% - 20px);max-width: calc(100% - 20px);margin-left: 0;}
}
.whatsappsWrap{margin-top: 30px;
    padding-left: 15px;
    padding-right: 15px;display: inline-flex;flex-direction: column;
}

.whatsappsWrap h2{
    text-transform: uppercase;
    color: #00AB44;
    margin-bottom: 10px;
}

.whatsappsWrap a{
    display: inline-block;
    margin-bottom: 10px;;
}
